.storybook-accordion {
    display: inline-block;
    cursor: pointer;
    border: 0;
    border-radius: 3em;
    font-weight: 700;
    line-height: 1;
    font-family: 'Nunito Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  }
  .storybook-accordion--primary {
    background-color: #555ab9;
    color: white;
  }
  .storybook-accordion--secondary {
    box-shadow: rgba(0, 0, 0, 0.15) 0px 0px 0px 1px inset;
    background-color: transparent;
    color: #333;
  }
  .storybook-accordion--small {
    padding: 10px 16px;
    font-size: 12px;
  }
  .storybook-accordion--medium {
    padding: 11px 20px;
    font-size: 14px;
  }
  .storybook-accordion--large {
    padding: 12px 24px;
    font-size: 16px;
  }
  